import Vue from 'vue'
import Router from 'vue-router'
import addGoods from '@/components/addGoods'
import setStatic from '@/components/setStatic'
import homePage from '@/components/homePage'
import checkGoods from '@/components/checkGoods'
import goodsDetail from '@/components/goodsDetail'
import dataSerch from '@/components/dataSerch'
import editDtl from '@/components/editDtl'
import dealDtl from '@/components/dealDtl'
import addOrder from '@/components/addOrder'
import svgMap from '@/components/svgMap'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'homePage',
      component: homePage
    },
    {
      path: '/addGoods',
      name: 'addGoods',
      component: addGoods
    },
    {
      path: '/setStatic',
      name: 'setStatic',
      component: setStatic
    },
    {
      path: '/checkGoods',
      name: 'checkGoods',
      component: checkGoods
    },
    {
      path: '/goodsDetail/:id',
      name: 'goodsDetail',
      component: goodsDetail
    },
    {
      path: '/dataSerch/',
      name: 'dataSerch',
      component: dataSerch
    },
    {
      path: '/editDtl/:id',
      name: 'editDtl',
      component: editDtl
    },
    {
      path: '/dealDtl/',
      name: 'dealDtl',
      component: dealDtl
    },
    {
      path: '/addOrder',
      name: 'addOrder',
      component: addOrder
    },
    {
      path: '/svgMap',
      name: 'svgMap',
      component: svgMap
    }
  ]
})



