
import $ from 'jquery'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import XImg from 'vux/src/components/x-img/index.vue'

export default {
directives: {
TransferDom
},
components: {
XInput,
XButton,
Group,
Flexbox,
FlexboxItem,
Swipeout,
SwipeoutItem,
SwipeoutButton,
XImg
},
name: 'dealDtl',
data () {
return {
orderList: []
}
},
methods: {
loadList () {
let self = this
self.orderList = JSON.parse(localStorage.getItem('order'))
},
onDel (id) {
let self = this
this.$vux.confirm.show({
title: '?',
content: '',
onCancel () {
console.log('plugin cancel')
},
onConfirm () {
$.each(self.orderList, function (k) {
if (self.orderList[k].orderId == id) {
self.orderList.splice(k, 1)
let obj = self.orderList
obj = JSON.stringify(obj)
localStorage.setItem('order', obj)
}
})
}
})
}
},
mounted () {
this.loadList()
}
}



