
import $ from 'jquery'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import XImg from 'vux/src/components/x-img/index.vue'

export default {
directives: {
TransferDom
},
components: {
XInput,
XButton,
Group,
Flexbox,
FlexboxItem,
Swipeout,
SwipeoutItem,
SwipeoutButton,
XImg
},
name: 'checkGoods',
data () {
return {
initData: [],
saveData: []
}
},
methods: {
onDetail (id) {
this.$router.push({path: '/goodsDetail/' + id})
},
onDel (id) {
let self = this
this.$vux.confirm.show({
title: '?',
content: '',
onCancel () {
console.log('plugin cancel')
},
onConfirm () {
$.each(self.initData, function (k) {
if (self.initData[k].id == id) {
self.initData.splice(k, 1)
}
})
self.saveData = JSON.stringify(self.initData)
localStorage.setItem('goods', self.saveData)
}
})
},
onEdite (id) {
this.$router.push({path: '/editDtl/' + id})
},
handleEvents (type) {
console.log('event: ', type)
}
},
beforeMount () {
this.initData = JSON.parse(localStorage.getItem('goods'))
},
mounted () {

}
}



