
import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Previewer from 'vux/src/components/previewer/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XImg from 'vux/src/components/x-img/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Picker from 'vux/src/components/picker/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import Alert from 'vux/src/components/alert/index.vue'

import $ from 'jquery'
export default {
directives: {
TransferDom
},
components: {
XInput,
XImg,
XButton,
Group,
Cell,
Previewer,
Swipeout,
SwipeoutItem,
SwipeoutButton,
GroupTitle,
PopupPicker,
Picker,
Selector,
Checker,
CheckerItem,
XTextarea,
Divider,
Flexbox,
FlexboxItem,
Confirm,
Alert
},
name: 'addOrder',
data () {
return {
status: true,
isOnly: true,
orderId: '',
orderDate: '',
orderData: [],
orderContent: '',
saveId: '',
saveBrand: '',
saveGender: '',
savePrice: '',
saveSize: '',
saveNum: '',
saveColor: '',
saveContent: '',
saveImg: '',
options: {
getThumbBoundsFn (index) {

let thumbnail = document.querySelectorAll('.previewer-demo-img')[index]

let pageYScroll = window.pageYOffset || document.documentElement.scrollTop


let rect = thumbnail.getBoundingClientRect()

return {x: rect.left, y: rect.top + pageYScroll, w: rect.width}


}
}
}
},
methods: {
showBox () {
this.status = !this.status
},
showOK () {
let self = this
self.isOnly = true
self.orderDate = self.getNowFormatDate()
let order = {
orderId: self.saveId,
orderBrand: self.saveBrand,
orderGender: self.saveGender,
orderPrice: self.savePrice,
orderSize: self.saveSize,
orderNum: self.saveNum,
orderColor: self.saveColor,
orderContent: self.saveContent,
orderDate: self.orderDate
}
if (localStorage.getItem('order')) {
self.orderData = localStorage.getItem('order')
self.orderData = JSON.parse(self.orderData)
$.each(self.orderData,function (k) {
if(self.orderData[k].orderId == self.saveId){
self.$vux.toast.show({
text: '',
type: 'warn'
})
self.isOnly = false
}
})
} else {
self.orderData.push(order)
}
if(self.isOnly){
self.orderData.unshift(order)
self.orderData = JSON.stringify(self.orderData)
localStorage.setItem('order', self.orderData)
self.$vux.toast.show({
text: ''
})
}
},
getNowFormatDate () {
let date = new Date();
let seperator1 = "-";
let seperator2 = ":";
let year = date.getFullYear();
let month = date.getMonth() + 1;
let strDate = date.getDate();
if (month >= 1 && month <= 9) {
month = "0" + month;
}
if (strDate >= 0 && strDate <= 9) {
strDate = "0" + strDate;
}
let currentdate = year + seperator1 + month + seperator1 + strDate
+ " " + date.getHours() + seperator2 + date.getMinutes()
+ seperator2 + date.getSeconds();
return currentdate;
},
showPlugin () {
let self = this
this.$vux.confirm.show({
title: '?',
content: '',
onCancel () {
console.log('plugin cancel')
},
onConfirm () {
self.$router.push({path: '/'})
}
})
},
},
mounted () {
}
}



