
import $ from 'jquery'
import Search from 'vux/src/components/search/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import XImg from 'vux/src/components/x-img/index.vue'
import Divider from 'vux/src/components/divider/index.vue'

export default {
components: {
Search,
Cell,
XInput,
XButton,
Group,
Flexbox,
FlexboxItem,
TransferDom,
Swipeout,
SwipeoutItem,
SwipeoutButton,
XImg,
Divider
},
name: 'dataSerch',
data () {
return {
initData: [],
results: [],
value: ''
}
},
methods: {
onDetail (id) {
this.$router.push({path: '/goodsDetail/' + id})
},
onDel (id) {
let self = this
this.$vux.confirm.show({
title: '?',
content: '',
onCancel () {
console.log('plugin cancel')
},
onConfirm () {
$.each(self.initData, function (k) {
if (self.initData[k].id == id) {
self.initData.splice(k, 1)
}
})
self.saveData = JSON.stringify(self.initData)
localStorage.setItem('goods', self.saveData)
self.onSubmit(self.value)
}
})
},
onEdite (id) {







},
setFocus () {
this.$refs.search.setFocus()
},
onSubmit (val) {
let self = this
self.results = []
$.each(self.initData, function (k) {
if (self.initData[k].id.indexOf(val) !== -1 || self.initData[k].stockBrand.indexOf(val) !== -1 || self.initData[k].stockGender[0].indexOf(val) !== -1 || self.initData[k].stockStatus[0].size.indexOf(val) !== -1) {

self.results.push(self.initData[k])
}
})
},
onCancel () {
this.results = []
},
},
mounted () {

},
beforeMount () {
this.initData = JSON.parse(localStorage.getItem('goods'))
}
}



