
import $ from 'jquery'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Previewer from 'vux/src/components/previewer/index.vue'
import XImg from 'vux/src/components/x-img/index.vue'

export default {
directives: {
TransferDom
},
components: {
XInput,
XButton,
Group,
Flexbox,
FlexboxItem,
Previewer,
XImg
},
name: 'checkGoods',
data () {
return {
initData: [],
showData: {},
list: [{'src': 'data:image/png;base64,'}],
options: {
getThumbBoundsFn (index) {

let thumbnail = document.querySelectorAll('.previewer-demo-img')[index]

let pageYScroll = window.pageYOffset || document.documentElement.scrollTop


let rect = thumbnail.getBoundingClientRect()

return {x: rect.left, y: rect.top + pageYScroll, w: rect.width}


}
}
}
},
methods: {
show (index) {
this.$refs.previewer.show(index)
}
},
beforeMount () {
let self = this
this.initData = JSON.parse(localStorage.getItem('goods'))

$.each(this.initData, function (i) {
if (self.initData[i].id == self.$route.params.id) {
self.showData = self.initData[i]
}
})
},
mounted () {

}
}



