

import XInput from 'vux/src/components/x-input/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Previewer from 'vux/src/components/previewer/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XImg from 'vux/src/components/x-img/index.vue'
import Swipeout from 'vux/src/components/swipeout/swipeout.vue'
import SwipeoutItem from 'vux/src/components/swipeout/swipeout-item.vue'
import SwipeoutButton from 'vux/src/components/swipeout/swipeout-button.vue'
import GroupTitle from 'vux/src/components/group-title/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Picker from 'vux/src/components/picker/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Divider from 'vux/src/components/divider/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import Alert from 'vux/src/components/alert/index.vue'

import $ from 'jquery'
export default {
directives: {
TransferDom
},
components: {
XInput,
XImg,
XButton,
Group,
Cell,
Previewer,
Swipeout,
SwipeoutItem,
SwipeoutButton,
GroupTitle,
PopupPicker,
Picker,
Selector,
Checker,
CheckerItem,
XTextarea,
Divider,
Flexbox,
FlexboxItem,
Confirm,
Alert
},
name: 'editDtl',
data () {
return {
saveId: '',
saveBrand: '',
saveGender: [],
savePrice: '',
saveSize: [],
saveNum: '',
saveColor: [],
saveContent: '',
saveImg: '',
imgDef: [{'src': 'data:image/jpeg;base64,'}],
saveStyle: [],
initStatic: {},
initData: {},
showToast: false,
hasSD: false,
showColor: [],
showGender: [],
showStyle: [],
showSize: [],
isNum: false,
gsData: [],
checkId: -1,
checkSize: -1,
checkColor: -1,

list: [{'src': 'data:image/png;base64,'}],
list2: 'data:image/jpeg;base64,',
options: {
getThumbBoundsFn (index) {

let thumbnail = document.querySelectorAll('.previewer-demo-img')[index]

let pageYScroll = window.pageYOffset || document.documentElement.scrollTop


let rect = thumbnail.getBoundingClientRect()

return {x: rect.left, y: rect.top + pageYScroll, w: rect.width}


}
}
}
},
methods: {
showUserImg () {
let self = this
let file = document.getElementById('sendImg').files[0]
let reader = new FileReader()
reader.readAsDataURL(file)
reader.onload = function () {
self.saveImg = this.result
}
},
loadData () {
this.initStatic = JSON.parse(localStorage.getItem('static'))
if (!this.initStatic) {
this.hasSD = true
} else {
this.showGender = this.initStatic.Gender
this.showColor = this.initStatic.Color
this.showStyle = this.initStatic.Style
this.showSize = this.initStatic.Size
}

},
showOK () {

let self = this
this.gsData = []
this.checkId = -1
this.checkSize = -1
this.checkColor = -1
if (JSON.parse(localStorage.getItem('goods'))) {
this.gsData=JSON.parse(localStorage.getItem('goods'))
}
if (this.saveId && this.saveSize && this.saveNum) {
let sendData = {
id: this.saveId,
stockBrand: this.saveBrand,
stockStatus: [{
size: this.saveSize[0],
type: [{
number: this.saveNum,
color: this.saveColor[0]
}]
}],
stockPrice: this.savePrice,
stockGender: this.saveGender,
stockContent: this.saveContent,
stockImg: [{'src': this.saveImg || this.imgDef}],
stockType: this.saveStyle
}


if (this.gsData.length != 0) {
$.each(this.gsData, function (i) {
if (self.gsData[i].id == self.saveId) {
self.checkId = i
$.each(self.gsData[i].stockStatus, function (s) {
if (self.gsData[i].stockStatus[s].size == self.saveSize) {
console.log(s, self.gsData[i].stockStatus[s].size)
self.checkSize = s
$.each(self.gsData[i].stockStatus[s].type, function (c) {
if (self.gsData[i].stockStatus[s].type[c].color == self.saveColor) {
self.$vux.toast.show({
text: '',
type: 'warn'
})
self.checkColor = c
}
})
}
})
}
})
} else {
this.gsData = [sendData]
self.gsData = JSON.stringify(self.gsData)
localStorage.setItem('goods', self.gsData)
this.$vux.toast.show({
text: ''
})
self.checkColor = 0
}
if (this.checkId == -1 && this.checkSize == -1 && this.checkColor == -1) {
this.gsData.push(sendData)
self.gsData = JSON.stringify(self.gsData)
localStorage.setItem('goods', self.gsData)
this.$vux.toast.show({
text: ''
})
} else if (this.checkId != -1 && this.checkSize == -1 && this.checkColor == -1) {
this.gsData[this.checkId].stockStatus.push(sendData.stockStatus[0])
self.gsData = JSON.stringify(self.gsData)
localStorage.setItem('goods', self.gsData)
this.$vux.toast.show({
text: ''
})
} else if (this.checkId != -1 && this.checkSize != -1 && this.checkColor == -1) {
this.gsData[this.checkId].stockStatus[this.checkSize].type.push(sendData.stockStatus[0].type[0])
self.gsData = JSON.stringify(self.gsData)
localStorage.setItem('goods', self.gsData)
this.$vux.toast.show({
text: ''
})
}
} else {
this.$vux.toast.show({
text: '',
type: 'warn'
})
}
},
noSD () {
this.$router.push({path: '/setStatic'})
},
showPlugin () {
let self = this
this.$vux.confirm.show({
title: '?',
content: '',
onCancel () {
console.log('plugin cancel')
},
onConfirm () {
self.$router.push({path: '/'})
}
})
},
onButtonClick (type) {
alert('on button click ' + type)
},
handleEvents (type) {
console.log('event: ', type)
},
show (index) {
this.$refs.previewer.show(index)
},
},
beforeMount () {
this.$route.params.id
},
mounted () {
this.loadData()
}
}



